import React, { useCallback, useEffect, useState } from "react";
import { useParams, useLocation } from "react-router-dom";
import ls from "store2";
import { getUserDetails, getUserToken } from "@/api/Users";
import { useCountStore } from "@/store/index";
import { useNavigate } from "react-router-dom";
import Loader from "@/Components/Loader";
import { getToken, setToken } from "@/utils/auth";
export default function UserProfileView() {
  const params = useParams();
  const location = useLocation();
  const { role } = location.state || {};
  const [loading, setLoading] = useState(true);
  const { setStoreRole, setUser } = useCountStore();
  const navigate = useNavigate();
  let param = params.id.split("-");
  const [userRole, setUserRole] = useState("");
  // Store the original token in localStorage when switching roles
  const storeOriginalToken = () => {
    let user = ls.get("user");
    const currentToken = getToken(); // Get the current token from storage

    if (!ls.get("rootToken")) {
      ls.set("rootToken", currentToken);
      ls.set("rootRole", user.role);
    }
  };

  // Switch to the new user's token
  const switchToNewToken = (newToken) => {
    setToken(newToken); // Replace the token with the new user's token
  };

  const getUserDetailAndSetRole = useCallback(() => {
    getUserDetails({
      id: param[0],
    })
      .then((res) => {
        if (res?.data?.role) {
          getUserDetail(res?.data?.role);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  const getUserDetail = useCallback(
    (role) => {
      if (param.length >= 2) {
        getUserToken({
          id: param[0],
          params: {
            role: role === "nursing_home" ? "nurse" : "supports_agent",
          },
        })
          .then(async (res) => {
            await storeOriginalToken();
            await setUpEverythingForViewProfile({
              ...res.data,
              sub_role: "elderly",
            });
            await switchToNewToken(res.data.access_token);
          })
          .catch((err) => {
            console.log(err);
          });
        getUserDetails({
          id: param[0],
        })
          .then((res) => {
            storeOriginalToken();
            if (res.data.role !== "nursing_home") {
              setUpEverythingForViewProfile({
                ...res.data,
                sub_role: "elderly",
              });
            }
          })
          .catch((err) => {
            console.log(err);
          });

        return;
      }
      getUserDetails({
        id: params.id,
      })
        .then((res) => {
          setUpEverythingForViewProfile(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
      getUserToken({
        id: params.id,
      })
        .then((res) => {
          storeOriginalToken();
          switchToNewToken(res.data.access_token);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    [role, params.id]
  );
  const nvto = (active) => {
    if (active == "super_admin") {
      navigate("/support-agent/dashboard/");
    } else if (active == "supports_agent") {
      navigate("/supporter/support-agent/dashboard");
    } else if (active == "nurse") {
      navigate("/supporter/nurse/dashboard");
    } else if (active == "end_user") {
      navigate("/supporter/end-user/dashboard");
    } else if (active == "elderly") {
      navigate(`/supporter/elderlies/elderly-profile/${param[1]}?tab=overview`);
    } else {
      navigate("/support-nurnt/dashboard");
    }
  };

  function setUpEverythingForViewProfile(data) {
    ls.set("user", data);
    ls.set("mainRole", data.role);
    ls.set("role", data.role);
    setStoreRole({ role: data.role });
    setUser({ role: data.role });
    setTimeout(() => {
      setLoading(false);
      nvto(data?.sub_role || data?.role);
    }, 1500);
  }

  useEffect(() => {
    getUserDetailAndSetRole();
  }, [getUserDetailAndSetRole]);
  if (loading) return <Loader loaderTitle="Loading User Profile" />;
}
